import React, { useState } from "react";
import {
  Form,
  FormGroup,
  ControlLabel,
  FormControl,
  ButtonToolbar,
  Button,
  Schema,
  Alert,
} from "rsuite";
import House from "../../interfaces/house";
import HouseService from "../../services/house-service";
const { StringType, NumberType } = Schema.Types;

function AddHouse() {
  const [formVal, setFormVal] = useState({});
  const model = Schema.Model({
    title: StringType().isRequired("This field is required"),
    price: NumberType().isRequired("This field is required"),
    country: StringType().isRequired("This field is required"),
    city: StringType().isRequired("This field is required"),
    description: StringType().isRequired("This field is required"),
  });
  async function submitForm() {
    let service = new HouseService();
    const res = await service.addHouse(formVal);
    console.log(res)
  }

  return (
    <Form
      model={model}
      formValue={formVal}
      onChange={(e) => {
        setFormVal(e);
      }}
      onSubmit={(e) => {
        submitForm();
      }}>
      <FormGroup>
        <ControlLabel>Title</ControlLabel>
        <FormControl name="title" />
      </FormGroup>
      <FormGroup>
        <ControlLabel>Price</ControlLabel>
        <FormControl name="price" type="number" />
      </FormGroup>
      <FormGroup>
        <ControlLabel>Country</ControlLabel>
        <FormControl name="country" />
      </FormGroup>
      <FormGroup>
        <ControlLabel>City</ControlLabel>
        <FormControl name="city" />
      </FormGroup>
      <FormGroup>
        <ControlLabel>Description</ControlLabel>
        <FormControl name="description" />
      </FormGroup>
      <FormGroup>
        <ButtonToolbar>
          <Button type="submit" appearance="primary">
            Submit
          </Button>
          <Button appearance="default">Cancel</Button>
        </ButtonToolbar>
      </FormGroup>
    </Form>
  );
}

export default AddHouse;
